export const booksReducer = (state = [...books], { type, payload }) => {
  switch (type) {
    case ADD_BOOK:
      return [...state, payload];
    case DELETE_BOOK:
      return state.filter(book => book.id !== payload);
    default:
      return state;
  }
};
